                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>fillRect(x, y, width, height)</code> 绘制实心矩形</p>
                                                            <p>2. <code>strokeRect(x, y, width, height)</code> 绘制空心矩形</p>
                                                            <p>3. <code>clearRect(x, y, width, height)</code> 清除一块矩形区域</p>
                                                            <p>4. <code>rect(x, y, width, height)</code> 绘制一个矩形路径，需要手动填充</p>
