                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{rectjs}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                    
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="path">使用路径可以绘制出任意多边形</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-path" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>beginPath()</code> 新建一条路径</p>
                                                            <p>2. <code>moveTo(x, y)</code> 将画笔移动到指定位置</p>
                                                            <p>3. <code>lineTo(x, y)</code> 绘制一条直线路径</p>
                                                            <p>4. <code>closePath()</code> 闭合路径</p>
                                                            <p>5. <code>fill()</code> 填充路径生成实心图形，不需要调用<code>closePath()</code></p>
                                                            <p>6. <code>stroke()</code> 描边，生成空心图形，需要调用<code>closePath()</code>闭合路径</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{pathhtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{pathjs}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="arc">绘制圆和圆弧</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-arc" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. 下面使用的方法全部都需要弧度，弧度和角度转换公式 <code>rad=(Math.PI/180)*ang</code></p>
                                                            <p>2. <code>arc(x, y, radius, startAngle, endAngle, anticlockwise)</code> 绘制一段圆弧，anticlockwise设置为true表示逆时针，默认是顺时针</p>
                                                            <p>3. <code>arcTo(x1, y1, x2, y2, radius)</code> 绘制两条线的切线圆弧，x1,y1表示第一条线的结束点第二条线的起始点，x2,y2表示第二条线的结束点，其中第一条线的起始点需要自己手动设置</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{archtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{arcjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="bezier">贝塞尔曲线</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-bezier" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. 贝塞尔曲线是绘制复杂优美线条的核心，关于贝塞尔曲线的知识请自行学习，在绘图的时候记住，曲线会向控制点的方向凹陷</p>
                                                            <p>2. <code>quadraticCurveTo(cp1x, cp1y, x, y)</code> 二次贝塞尔曲线，(cp1x,cp1y)表示控制点，(x,y)表示结束点，开始点要手动设置</p>
                                                            <p>3. <code>bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x, y)</code> 三次贝塞尔曲线，原理同上</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{bezierhtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{bezierjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
