                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            title: "canvas基础应用",
            items: [
                {
                    title: "绘制实心矩形",
                    target: "#rect"
                },
                { divider: true },
                {
                    title: "使用路径可以绘制出任意多边形",
                    target: "#path"
                },
                { divider: true },
                {
                    title: "绘制圆和圆弧",
                    target: "#arc"
                },
                { divider: true },
                {
                    title: "贝塞尔曲线",
                    target: "#bezier"
                }
            ],
            recthtml: `
            <canvas 
                id="canvas-rect" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            rectjs: `
            const canvasRect = document.getElementById('canvas-rect');
            const ctxR = canvasRect.getContext('2d');
            ctxR.fillStyle = 'green';
            ctxR.strokeStyle = 'darkseagreen';
            // 方式一
            ctxR.fillRect(10, 10, 80, 50);
            // 方式二
            ctxR.rect(10, 70, 80, 50);
            ctxR.fill();
            // 方式三
            ctxR.strokeRect(10, 130, 80, 50);
            // 方式四
            ctxR.fillRect(120, 10, 80, 110);
            ctxR.clearRect(140, 30, 40, 70);
            `,
            pathhtml: `
            <canvas 
                id="canvas-path" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            pathjs: `
            const canvasPath = document.getElementById('canvas-path');
            const ctxP = canvasPath.getContext('2d');
            ctxP.fillStyle = 'green';
            ctxP.strokeStyle = 'darkseagreen';
            // 实心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 10);
            ctxP.lineTo(80, 10);
            ctxP.lineTo(90, 30);
            ctxP.lineTo(80, 50);
            ctxP.lineTo(30, 50);
            ctxP.lineTo(20, 30);
            ctxP.fill();
            // 空心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 70);
            ctxP.lineTo(80, 70);
            ctxP.lineTo(90, 90);
            ctxP.lineTo(80, 110);
            ctxP.lineTo(30, 110);
            ctxP.lineTo(20, 90);
            ctxP.closePath();
            ctxP.stroke();
            `,
            archtml: `
            <canvas 
                id="canvas-arc" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            arcjs: `
            const canvasArc = document.getElementById('canvas-arc');
            const ctxA = canvasArc.getContext('2d');
            ctxA.fillStyle = 'green';
            ctxA.strokeStyle = 'darkseagreen';

            // 绘制圆
            ctxA.beginPath();
            ctxA.arc(50, 50, 40, 0, (Math.PI/180)*360, false);
            ctxA.fill();

            // 绘制圆切片
            ctxA.beginPath();
            ctxA.arc(50, 140, 40, 0, (Math.PI/180)*120, true);
            ctxA.fill();

            // 绘制圆角
            ctxA.beginPath();
            ctxA.moveTo(190, 10);
            ctxA.arcTo(110, 10, 110, 70, 30);
            ctxA.lineTo(110, 70);
            ctxA.stroke();
            `,
            bezierhtml: `
            <canvas 
                id="canvas-bezier" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            bezierjs: `
            const canvasBezier= document.getElementById('canvas-bezier');
            const ctxB = canvasBezier.getContext('2d');
            ctxB.fillStyle = 'green';
            ctxB.strokeStyle = 'darkseagreen';

            // 二次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(30, 10);
            ctxB.quadraticCurveTo(50, 20, 30, 30);
            ctxB.quadraticCurveTo(10, 20, 30, 10);
            ctxB.moveTo(30, 30);
            ctxB.quadraticCurveTo(70, 50, 30, 70);
            ctxB.quadraticCurveTo(-10, 50, 30, 30);
            ctxB.stroke();

            // 三次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(10, 100);
            ctxB.bezierCurveTo(30, 80, 70, 120, 100, 100);
            ctxB.stroke();
            `
        };
    },
    mounted() {
        this.drawRect();
        this.drawPath();
        this.drawArc();
        this.drawBezier();
    },
    methods: {
        drawRect() {
            const canvasRect = document.getElementById('canvas-rect');
            const ctxR = canvasRect.getContext('2d');
            ctxR.fillStyle = 'green';
            ctxR.strokeStyle = 'darkseagreen';
            // 方式一
            ctxR.fillRect(10, 10, 80, 50);
            // 方式二
            ctxR.rect(10, 70, 80, 50);
            ctxR.fill();
            // 方式三
            ctxR.strokeRect(10, 130, 80, 50);
            // 方式四
            ctxR.fillRect(120, 10, 80, 110);
            ctxR.clearRect(140, 30, 40, 70);
        },
        drawPath() {
            const canvasPath = document.getElementById('canvas-path');
            const ctxP = canvasPath.getContext('2d');
            ctxP.fillStyle = 'green';
            ctxP.strokeStyle = 'darkseagreen';
            // 实心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 10);
            ctxP.lineTo(80, 10);
            ctxP.lineTo(90, 30);
            ctxP.lineTo(80, 50);
            ctxP.lineTo(30, 50);
            ctxP.lineTo(20, 30);
            ctxP.fill();
            // 空心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 70);
            ctxP.lineTo(80, 70);
            ctxP.lineTo(90, 90);
            ctxP.lineTo(80, 110);
            ctxP.lineTo(30, 110);
            ctxP.lineTo(20, 90);
            ctxP.closePath();
            ctxP.stroke();
        },
        drawArc() {
            const canvasArc = document.getElementById('canvas-arc');
            const ctxA = canvasArc.getContext('2d');
            ctxA.fillStyle = 'green';
            ctxA.strokeStyle = 'darkseagreen';

            // 绘制圆
            ctxA.beginPath();
            ctxA.arc(50, 50, 40, 0, (Math.PI/180)*360, false);
            ctxA.fill();

            // 绘制圆切片
            ctxA.beginPath();
            ctxA.arc(50, 140, 40, 0, (Math.PI/180)*120, true);
            ctxA.fill();

            // 绘制圆角
            ctxA.beginPath();
            ctxA.moveTo(190, 10);
            ctxA.arcTo(110, 10, 110, 70, 30);
            ctxA.lineTo(110, 70);
            ctxA.stroke();
        },
        drawBezier() {
            const canvasBezier= document.getElementById('canvas-bezier');
            const ctxB = canvasBezier.getContext('2d');
            ctxB.fillStyle = 'green';
            ctxB.strokeStyle = 'darkseagreen';

            // 二次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(30, 10);
            ctxB.quadraticCurveTo(50, 20, 30, 30);
            ctxB.quadraticCurveTo(10, 20, 30, 10);
            ctxB.moveTo(30, 30);
            ctxB.quadraticCurveTo(70, 50, 30, 70);
            ctxB.quadraticCurveTo(-10, 50, 30, 30);
            ctxB.stroke();

            // 三次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(10, 100);
            ctxB.bezierCurveTo(30, 80, 70, 120, 100, 100);
            ctxB.stroke();
        }
    }
};
</script>

